<template>
    <div class="blogs">
        <h1>博客</h1>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    computed: {
      ...mapState([
          "allnavs", //导航数据
      ]),  
    },
    data () {
        return {
            
        }
    },
    methods: {
        
    },
    created () {
        
    },
    watch: {
    allnavs: {
      handler(allnavs) {
        // 判断是否允许访问
        if (allnavs.navs && allnavs.navs.split(",")[1] != 1) {
          return this.$router.push("/");
        }
      },
      // 深度监听
      deep: true
    }
  }
}
</script>

